<template>
  <div class="classifyList">
    <van-nav-bar
      ref="toolbar"
      style="background:#ffffff"
      left-arrow
      :title="major"
      @click-left="back"
    />
    <van-tree-select
      :items="items"
      :active-id.sync="activeId"
      :main-active-index.sync="activeIndex"
      @click-nav="select"
    >
      <template #content>
        <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
          <div class="books" v-for="(item,index) in list" :key="index" @click="goDetail(item._id)">
            <div class="img-box">
              <img class="auto-img" :src="item.cover" alt />
            </div>
            <div class="text-box">
              <div class="title-box">
                <div class="title">{{item.title}}</div>
                <div class="minorCate">{{item.minorCate}}</div>
              </div>
              <div class="hot-box">
                <div class="rankno">{{index+1}}</div>
                <div class="latelyFollower">{{item.latelyFollower}}热度</div>
              </div>
            </div>
          </div>
        </van-list>
      </template>
    </van-tree-select>
  </div>
</template>

<script>
import "../assets/less/rank.less";
export default {
  data() {
    return {
      items: [{ text: "分组 1" }, { text: "分组 2" }],
      activeId: 1,
      activeIndex: 0,
      booksData: [],
      list: [],
      loading: false,
      finished: false,
    };
  },
  computed: {
    major() {
      let key = this.$route.query.title;
      return this.$store.state.rankStore.categoriesData[key].tab;
    },
  },
  created() {
    this.setitems();
  },
  methods: {
    setitems() {
      let rankData = this.$store.state.rankStore.categoriesData[
        this.$route.query.title
      ].data;
      this.items = [];
      rankData.map((v) => {
        this.items.push({ text: v.shortTitle, id: v._id });
      });
      this.booksData = rankData[0].booksData;
      // console.log(rankData[0].booksData);
      // console.log("this.items==>", this.items);
    },
    back() {
      this.$router.go(-1);
    },

    // 懒加载
    onLoad() {
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      setTimeout(() => {
        for (let i = 0; i < 10; i++) {
          this.list.push(this.booksData[this.list.length]);
        }
        // 加载状态结束
        this.loading = false;
        // 数据全部加载完成
        if (this.list.length >= this.booksData.length) {
          this.finished = true;
        }
      }, 1000);
    },

    // 点击选项
    select(index) {
      this.booksData = [];
      this.list = [];
      this.loading = true;
      this.finished = false;
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 1000,
        loadingType: "spinner",
      });
      // console.log(this.items[index]);
      this.axios({
        method: "GET",
        url: "/rank/" + this.items[index].id,
      }).then((result) => {
        this.$toast.clear();
        this.booksData = result.data.ranking.books;
        this.booksData.map((v) => {
          v.cover = unescape(v.cover.slice(7));
        });
        this.booksData.sort((a,b)=>{
          return b.latelyFollower - a.latelyFollower
        })
        // console.log(this.booksData);
        this.onLoad();
      });
    },

    // 点击书籍，根据书籍id获取书籍详情，前往书籍详情页面
    goDetail(id) {
      this.$store.dispatch("detailStore/setBookIdListById", id);
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios
        .all([
          this.axios({
            method: "GET",
            url: "/book-info/" + id,
          }),
          this.axios({
            method: "GET",
            url: "/book-sources?view=summary&book=" + id,
          }),
        ])
        .then((result) => {
          this.$toast.clear();
          this.$store.dispatch("detailStore/setBookDetail", result[0].data);
          this.$store.dispatch(
            "detailStore/setBookOriginIdListById",
            result[1].data[0]._id
          );
          this.$router.push({ name: "Detail" });
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
  },
};
</script>
